.container {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 8.16px 8px 0 8px;
  padding: 0.51rem 0.5rem 0 0.5rem;
}
.container .title {
  font-size: 9.6px;
  font-size: 0.6rem;
}
.container .tel-zone {
  margin-top: 12.8px !important;
  margin-top: 0.8rem !important;
}
.container .bind-btn {
  width: 100%;
  height: 12.8px;
  height: 0.8rem;
  margin-top: 12.8px;
  margin-top: 0.8rem;
  font-size: 5.76px;
  font-size: 0.36rem;
  letter-spacing: 3.2px;
  letter-spacing: 0.2rem;
  border-radius: 0.08rem;
  border: 1px solid #333;
}
.container .input-zone {
  position: relative;
  width: 100%;
  margin-top: 4.8px;
  margin-top: 0.3rem;
}
.container .input-zone .code-btn {
  position: absolute;
  display: inline-block;
  right: 0.32px;
  right: 0.02rem;
  top: 1.6px;
  top: 0.1rem;
  width: 24.8px;
  width: 1.55rem;
  height: 8px;
  height: 0.5rem;
  cursor: pointer;
  text-align: center;
  line-height: 8px;
  line-height: 0.5rem;
  font-size: 3.2px;
  font-size: 0.2rem;
  color: #333;
  border: 1px solid #333;
}
.container .input-zone .code-btn-disabled {
  border-color: #a8a4a4;
  color: #a8a4a4;
  pointer-events: none;
}
.container input {
  display: block;
  width: 100%;
  height: 12.8px;
  height: 0.8rem;
  padding: 0;
  border-radius: 0;
  border: none;
  outline: none;
  letter-spacing: normal;
  word-spacing: normal;
  line-height: 6.4px;
  line-height: 0.4rem;
  text-transform: none;
  text-indent: 0px;
  text-shadow: none;
  font-size: 4.8px;
  font-size: 0.3rem;
  color: #333;
  border-bottom: 1px solid #333;
}
.container input.focus {
  border-bottom: #333;
}
