

































































































































.container {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 0.51rem 0.5rem 0 0.5rem;
  .title {
    font-size: 0.6rem;
  }
  .tel-zone {
    margin-top: 0.8rem !important;
  }
  .bind-btn {
    width: 100%;
    height: 0.8rem;
    margin-top: 0.8rem;
    font-size: 0.36rem;
    letter-spacing: 0.2rem;
    border-radius: 0.08rem;
    border: 1px solid #333;
  }
  .input-zone {
    position: relative;
    width: 100%;
    margin-top: 0.3rem;
    .code-btn {
      position: absolute;
      display: inline-block;
      right: 0.02rem;
      top: 0.1rem;
      width: 1.55rem;
      height: 0.5rem;
      cursor: pointer;
      text-align: center;
      line-height: 0.5rem;
      font-size: 0.2rem;
      color: #333;
      border: 1px solid #333;
    }
    .code-btn-disabled {
      border-color: #a8a4a4;
      color: #a8a4a4;
      pointer-events: none;
    }
  }
  input {
    display: block;
    width: 100%;
    height: 0.8rem;
    padding: 0;
    border-radius: 0;
    border: none;
    outline: none;
    letter-spacing: normal;
    word-spacing: normal;
    line-height: 0.4rem;
    text-transform: none;
    text-indent: 0px;
    text-shadow: none;
    font-size: 0.3rem;
    color: #333;
    border-bottom: 1px solid #333;
    &.focus {
      border-bottom: #333;
    }
  }
}
